import { Component, OnInit } from '@angular/core';
import { HistorisationService } from '../historisation.service';

@Component({
  selector: 'app-historisation-parcours',
  templateUrl: './historisation-parcours.page.html',
  styleUrls: ['./historisation-parcours.page.scss'],
})
export class HistorisationParcoursPage implements OnInit {

  constructor(private HisServices : HistorisationService) { }

  EspacesArray=[];
  newDate:Date;

  ngOnInit() {
    this.newDate = new Date((new Date()).valueOf() - 1000*60*60*24);
    this.EspacesArray = this.HisServices.getEspaces();
  }
}
